import logo from './logo.svg';
import './App.css';
import { GeistProvider, CssBaseline, Dot, Tooltip } from '@geist-ui/core'
function App() {
  var dottype = "warning"
  var status = "idle"
  fetch("https://536644802595520534")
    .then(response => {
        return response.json()
    }).then(json => {
      if (json["activities"][0] != null) {
        switch (json["discord_status"]) {
          case "online":
            dottype = "success";
            status = "online"
            break;
          case "offline":
            dottype = null
            break;
          }
      }
    })
    .catch(error => {
        dottype = null
        status = "offline"
    });


  return (
    <div className="App">
      <header className="App-header">
        <img src={logo} className="App-logo" alt="logo" />
        <h1 className="text-bold">heyo, i'm thrizzle <Tooltip text={status} type="dark" hideArrow placement="right"><Dot type={dottype}></Dot></Tooltip></h1>
        <a
          className="App-link"
          href="https://reactjs.org"
          target="_blank"
          rel="noopener noreferrer"
        >
          Learn React
        </a>
      </header>
    </div>
  );
}

export default () => (
  <GeistProvider>
    <CssBaseline />
    <App />
  </GeistProvider>
)
